<template>
  <div class="form_box">
    <el-form ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <template>
        <div class="change_box">
          <el-form-item size="small" label="商品类型：">
            <el-select
              placeholder="请选择"
              v-model="selectcateId"
              @change="selectOption"
            >
              <el-option
                v-for="item in cateAttrList"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="商品规格：">
            <el-card
              shadow="never"
              body-style="background-color:#f8f9fc;width:100%"
            >
              <div
                v-for="(item, index) in spguige"
                style="width: 558px; margin-top: 10px"
              >
                <p>{{ item.name }}</p>
                <p>
                  <el-checkbox-group v-model="checkList">
                    <el-checkbox
                      v-for="(item2, index2) in item.inputList"
                      :label="item2"
                    ></el-checkbox>
                  </el-checkbox-group>
                </p>
                <p v-if="item.inputType == 0">
                  <!--getInputText收集每次输入的颜色值，推进新数组colorArry备用-->
                  <el-input
                    style="width: 160px"
                    size="small"
                    v-model="getInputText"
                    @keydown.enter.native="addcolor"
                  ></el-input>
                  <el-button
                    size="small"
                    style="margin-left: 20px"
                    @click="addcolor"
                    >增加</el-button
                  >
                </p>
              </div>
            </el-card>
          </el-form-item>

          <el-form-item>
            <el-table
              :data="tableData"
              border
              style="width: 100%; margin-top: 20px"
              empty-text="暂无数据"
            >
              <el-table-column
                align="center"
                width="90"
                v-for="(item, index) in newSpguige"
                :label="item.name"
              >
                <template v-slot="vs">
                  {{ vs.row.spData[item.name] }}
                </template>
              </el-table-column>
              <el-table-column align="center" label="销售价格" width="90">
                <template v-slot="vs">
                  <el-input v-model="vs.row.price"></el-input>
                </template>
              </el-table-column>
              <el-table-column align="center" label="商品库存" width="91">
                <template v-slot="vs">
                  <el-input v-model="vs.row.stock" align="center"></el-input>
                </template>
              </el-table-column>
              <el-table-column align="center" label="库存预警值" width="100">
                <template v-slot="vs">
                  <el-input v-model="vs.row.lowStock"></el-input>
                </template>
              </el-table-column>
              <el-table-column align="center" label="SKU编号" width="128">
                <template v-slot="vs">
                  <el-input v-model="vs.row.skuCode"></el-input>
                </template>
              </el-table-column>
              <el-table-column align="center" label="操作" width="80">
                <template v-slot="vs">
                  <span @click="deleteTabledata(vs.$index)">删除</span>
                </template>
              </el-table-column>
            </el-table>
          </el-form-item>

          <el-form-item>
            <el-button
              type="primary"
              style="margin-top: 20px; font-size: 14px"
              size="small"
              @click="getSelectInfo"
              >刷新列表</el-button
            >
            <el-button
              type="primary"
              style="margin-top: 20px; font-size: 14px"
              size="small"
              @click="syncPrice"
              >同步价格</el-button
            >
            <el-button
              type="primary"
              style="margin-top: 20px; font-size: 14px"
              size="small"
              >同步库存</el-button
            >
          </el-form-item>

          <el-form-item label="商品参数:" style="margin-top: 20px">
            <el-card shadow="never" body-style="background-color:#f8f9fc">
              <p
                v-for="(item, index) in spCanshu"
                :key="index"
                v-if="item.inputType == 0"
              >
                {{ item.name }}:&nbsp;&nbsp;
                <el-input
                  style="width: 160px"
                  size="small"
                  v-model="getspCanshu[index].value"
                ></el-input>
              </p>

              <p
                v-for="(item, index) in spCanshu"
                :key="index"
                v-if="item.inputType == 1"
              >
                {{ item.name }}:&nbsp;&nbsp;
                <el-select
                  placeholder="请选择"
                  v-model="getspCanshu[index].value"
                  @change="selectCanshu"
                >
                  <el-option
                    v-for="val in item.inputList"
                    :label="val"
                    :value="val"
                  ></el-option>
                </el-select>
              </p>
            </el-card>
          </el-form-item>

          <el-form-item label="商品相册:" style="margin-top: 20px">
            <el-upload
              class="avatar-uploader"
              action="http://8.129.190.90/fileUpload"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
            >
              <img v-if="imageUrl" :src="imageUrl" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>

          <el-form-item label="规格参数:" style="margin-top: 60px">
            <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
              <el-tab-pane label="电脑端详情" name="first">
                <div id="div1">
                  <p>
                    <b>
                      <textarea
                        id="text1"
                        style="width: 100%; height: 200px"
                      ></textarea>
                    </b>
                  </p>
                </div>
              </el-tab-pane>
              <el-tab-pane label="移动端详情" name="second">
                <div id="div2">
                  <p><b></b></p>
                </div>
              </el-tab-pane>
            </el-tabs>
          </el-form-item>

          <div class="click_box">
            <el-button
              type="primary"
              style="margin-top: 12px; font-size: 14px"
              @click="prev_step"
              size="small"
              >上一步,填写商品促销</el-button
            >
            <el-button
              type="primary"
              style="margin-top: 12px; font-size: 14px"
              @click="next_step"
              size="small"
              >下一步,选择商品关联</el-button
            >
          </div>
        </div>
      </template>
    </el-form>
  </div>
</template>
<script>
//注意先拿一下skudata数据用，然后清除颜色那边的数据，再把整理分类好的推进去，类似于让inputList=收集input填写颜色收集的colorArry来达到新增的效果
import { getPmsCatewithAttr } from "../../../../../api/productcateAttr.js";
import { productAttributelist } from "../../../../../api/shop.js";
import E from "wangeditor";
export default {
  props: ["vs"],
  props: ["productParam"],
  data() {
    return {
      getspCanshu:[],
      spCanshu: [],
      tableData: [],
      arr: [],
      vs1: {},
      newSpguige: [],
      colorArry: [],
      getInputText: "",
      checkList: [],
      selectcateId: "",
      cateAttrList: [],
      spguige: [],
      spguige_inputLis: [],
      queryparams: {
        pageNum: 1,
        pageSize: 5,
      },
      imageUrl: "",
      activeName: "first", //el-tabs
    };
  },
  methods: {

    //删除
    deleteTabledata(index) {
      // console.log("index", index);
      this.tableData.splice(index, 1);
    },

    //同步价格
    syncPrice() {
      // console.log("sync的tabledata", this.tableData);
    },

    //增加
    addcolor() {
      // console.log("getInputText", this.getInputText);
      // console.log(typeof this.getInputText);
      this.colorArry.push(this.getInputText);
      this.getInputText = "";
      // console.log("this.colorArry", this.colorArry);
      this.spguige.forEach((item) => {
        if (item.inputType == 0) {
          item.inputList = this.colorArry;
          //让inputList=收集input填写颜色收集的colorArry来达到新增的效果
        }
      });
      // console.log("推进去之后的spguige", this.spguige);
    },

    //刷新列表
    getSelectInfo() {
      (this.tableData = []),
        (this.newSpguige = []),
        (this.vs1 = []),
      //   console.log("用来比较的spguige", this.spguige);
      // console.log("this.checkList", this.checkList);
      this.spguige.forEach((item) => {
        var new1 = {};
        new1["name"] = item.name;
        new1["inputList"] = [];
        this.newSpguige.push(new1); //给newSpguige增加name属性和inputList属性，与原始数据保持一致
        this.newSpguige.forEach((item1) => {
          this.checkList.forEach((item2) => {
            if (
              item.inputList.includes(item2) &&
              item.name == item1.name //判断分别拿出对应的推到newSpguige里面
            ) {
              item1.inputList.push(item2);
            }
          });
          this.vs1[item1.name] = item1.inputList;//将[{name:'颜色',inputList:['','','']}；,]变成 颜色:['','',''] 容量:['','','']
        });
      });

      // console.log("vs1", this.vs1);
      // console.log("newSpguige", this.newSpguige);

      //开始交叉
      var aa = [];
      var bb = [];
      for (var O in this.vs1) {
        if (aa.length == 0) {
          this.vs1[O].forEach((item) => {
            aa.push({ [O]: item });
          });
        } else {
          this.vs1[O].forEach((item) => {
            var _aa = JSON.parse(JSON.stringify(aa));
            _aa.forEach((i) => {
              i[O] = item;
              bb.push(i);
            });
          });
          aa = JSON.parse(JSON.stringify(bb));
          bb = [];
          
        }
      }
      aa.forEach(item => {//要什么格式的数据就var新的来装
        var td={
          lowStock:'',
          price:'',
          skuCode:'',
          spData:null,
          stock:'',
        }
        td.spData=item
        this.tableData.push(td)
      });
      console.log('vs1',this.vs1)
      console.log('spdata',this.spdata)
      console.log("aa", aa);
      console.log("this.tableData", this.tableData);
    },

    selectCanshu(){
      console.log('getspCanshu',this.getspCanshu)
    },

    //切换选择属性类型
    selectOption() {
      (this.spCanshu = []), (this.tableData = []), (this.colorArry = []);
      this.checkList = [];
      (this.newSpguige = []), console.log("点击时的id", this.selectcateId);
      //获得商品规格/属性 颜色 容量 尺寸这些
      productAttributelist({
        cid: this.selectcateId,
        pageNum: null,
        pageSize: null,
        type: 0,//0是属性
      }).then((resp) => {
        console.log("productAttributelist的resp", resp);
        this.spguige = resp.data.list;
        //inputList是字符串，把他变数组
        this.spguige.forEach((item) => {
          if (item.inputType == 0) {
            item.inputList = [];
          } else {
            item.inputList = item.inputList.split(",");
          }
        });
        console.log("this.spguige", this.spguige);
      });

      //获得商品属性参数，网络 手机系统 这些
      productAttributelist({
        cid: this.selectcateId,
        pageNum: null,
        pageSize: null,
        type: 1,//1是参数
      }).then((resp) => {
        this.spCanshu = resp.data.list;
        this.spCanshu.forEach((item) => {
          if (item.inputType == 0) {
            item.inputList = [];
          } else {
            item.inputList = item.inputList.split(",");
          }
            var valuelist={
              productAttributeId:item.id,
              value:null
            }
            this.getspCanshu.push(valuelist)
        });
            console.log("商品参数", this.spCanshu);
      });
    },
    handleClick(tab, event) {
      console.log(tab, event);
    },
    //el-tabs方法
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
      console.log('ssssss',res);
      this.productParam.pic='http://8.129.190.90'+res.msg
      console.log(this.productParam.pic)
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传头像图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isJPG && isLt2M;
    },
    prev_step() {
      this.info_step = 1;
      this.$emit("trans_infostep", this.info_step);
    },
    next_step() {
      this.info_step = 3;
      this.$emit("trans_infostep", this.info_step);
      // this.tableData.forEach(item => {
      //   item.spData=JSON.stringify(item.spData)
      // });

      // 把spData转为符合要求的格式spData: "[{\"key\":\"颜色\",\"value\":\"红\"},{\"key\":\"尺寸\",\"value\":\"X\"}]"
      var _tableData=JSON.parse(JSON.stringify(this.tableData))
      _tableData.forEach(item => {
        var a=[]
        for(var b in this.vs1){
          var c=item.spData[b]//注意这样的形式转换数据会经常用到
          a.push({'key':b,'value':c})
        }
        item.spData=JSON.stringify(a)
      });
        console.log("vs1", this.vs1);
        console.log("newSpguige", this.newSpguige);
        console.log('最新getspCanshu',this.getspCanshu)
        console.log('最新this.tableData',this.tableData)
        this.productParam.skuStockList=_tableData
        // this.productParam.skuStockList=this.tableData
        this.productParam.productAttributeValueList=this.getspCanshu
        this.productParam.productAttributeCategoryId=this.selectcateId
        console.log('this.productParam',this.productParam)
    },
  },
  mounted() {
    //获得商品类型
    getPmsCatewithAttr().then((resp) => {
      console.log("getPmsCatewithAttr", resp);
      this.cateAttrList = resp.data;
      console.log("this.cateAttrList", this.cateAttrList);
    });
    //注册富文本
    const editor = new E("#div1");
    editor.create();
    const editor2 = new E("#div2");
    editor2.create();
  },
};
</script>

<style scoped='scoped'>
.avatar-uploader {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  width: 148px;
  background-color: #fbfdff;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 148px;
  height: 148px;
  line-height: 148px;
  text-align: center;
}
.avatar {
  width: 148px;
  height: 148px;
  display: block;
}
.table {
  margin-top: 20px;
  text-align: center;
}
.click_box {
  margin-left: 170px;
}
.form_box {
  padding-top: 20px;
  width: 100%;
  float: left;
}
</style>
