<template>
  <div>
    <div class="datalist">
      <span class="dataliest-span1"
        ><i class="el-icon-tickets"></i> 数据列表</span
      >
      <span class="dataliest-span2"
        ><el-button size="small" @click="add">添加</el-button></span
      >
    </div>
    <div class="biao">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column prop="id" align="center" label="编号" width="100">
        </el-table-column>
        <el-table-column
          prop="name"
          align="center"
          label="分类名称"
          width="385"
        >
          <template v-slot="vs">
            <p>{{ vs.row.name }}</p>
          </template>
        </el-table-column>

        <el-table-column prop="level" align="center" label="级别" width="100">
          <template v-slot="vs">
            <p>{{ vs.row.level }}</p>
          </template>
        </el-table-column>

        <el-table-column
          prop="productCount"
          align="center"
          label="商品数量"
          width="100"
        >
          <template v-slot="vs">
            <p>{{ vs.row.productCount }}</p>
          </template>
        </el-table-column>

        <el-table-column
          prop="productUnit"
          align="center"
          label="数量单位"
          width="100"
        >
          <template v-slot="vs">
            <p>{{ vs.row.productUnit }}</p>
          </template>
        </el-table-column>

        <el-table-column align="center" label="导航栏" width="100">
          <template v-slot="vs">
            <p>
              <el-switch
                :active-value="1"
                :inactive-value="0"
                v-model="vs.row.navStatus"
                active-color="#409eff"
                inactive-color="#dcdfe6"
                @change="handlenavStatus(vs.row.id,vs.row.navStatus)"
              >
              </el-switch>
            </p>
          </template>
        </el-table-column>

        <el-table-column align="center" label="是否显示" width="100">
          <template v-slot="vs">
            <p>
              <el-switch
                :active-value="1"
                :inactive-value="0"
                v-model="vs.row.showStatus"
                active-color="#409eff"
                inactive-color="#dcdfe6"
                @change="handleshowStatus(vs.row.id,vs.row.showStatus)"
              >
              </el-switch>
            </p>
          </template>
        </el-table-column>

        <el-table-column prop="sort" align="center" label="排序" width="100">
        </el-table-column>

        <el-table-column align="center" label="设置" width="200">
          <template v-slot="vs">
            <el-button size="mini" @click="checkchildren(vs.row.id)" :disabled="panduan"
              >查看下级</el-button
            >
            <el-button size="mini">转移商品</el-button>
          </template>
        </el-table-column>

        <el-table-column align="center" label="操作" width="184">
          <template v-slot="vs">
            <p>
              <el-button size="mini" @click="edit">编辑</el-button>
              <el-button size="mini" type="danger" @click="dProduct(vs.row.id)"
                >删除</el-button
              >
            </p>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="fenye">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="this.queryparams.pageNum"
        :page-sizes="[2, 3, 4, 5]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getPmsCateAll } from "../../../../api/productcateAll.js";
import { productCateDelete } from "../../../../api/productCateDelete.js";
import { getPmsCateWithChildren } from "../../../../api/productcate.js";
import { changenavStatus } from "../../../../api/productcate.js";
import { changeshowStatus } from "../../../../api/productcate.js";
export default {
  data() {
    return {
      panduan: false,
      getselectId: "",
      withChildren: [],
      children: [],
      total: 0,
      tableData: [],
      dtable: {},
      value: [],
      options: [],
      queryparams: {
        pageNum: 1,
        pageSize: 5,
        parentId: 0,
      },
    };
  },

  methods: {
    handlenavStatus(ids,navStatus){
      console.log(ids, status);
      changenavStatus({
        ids: ids,
        navStatus: navStatus,
      }).then(() => {
        this.getclist();
      });
    },

    handleshowStatus(ids,showStatus){
      console.log(ids, status);
      changeshowStatus({
        ids: ids,
        showStatus: showStatus,
      }).then(() => {
        this.getclist();
      });
    },

    edit() {
      this.$router.push("/shop/shopcateedit");
    },
    //查看下级
    getclistwithchild() {
      //获取所有的一级分类以及子分类
      getPmsCateWithChildren().then((resp) => {
        // console.log("withchildren", resp);
        this.withChildren = resp.data;
        console.log("this.withChildren", this.withChildren);
      });
    },
    //点击 查看下级
    checkchildren(id) {
      this.getselectId = id;
      console.log("点击查看下级时的id", id);
      this.$router.push({
        path: "shopcate",
        query: {
          parentId: id,
        },
      });
      this.queryparams = {
        pageNum: 1,
        pageSize: 5,
        parentId: id,
      };
      this.getclist();
      this.children = [];
      this.withChildren.forEach((item) => {
        if (item.id == id) {
          this.children.push(item.children);
        }
      });
      console.log("this.children", this.children);
    },

    add() {
      this.$router.push("/shop/shopcateadd");
    },

    getclist() {
      getPmsCateAll(this.queryparams).then((resp) => {
        console.log("cateresp", resp);
        this.tableData = resp.data.list;
        console.log("this.tableData", this.tableData);
        this.total = resp.data.total;
      });
    },

    dProduct(id) {
      console.log("idididi", id);
      this.$confirm("请再次确定操作，是否删除该商品?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "删除成功!",
          }); //删除单个商品
          productCateDelete(id).then(() => {
            this.getclist();
            // this.getclistwithchild()
            this.$router.go(0);
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },

    handleSizeChange(data) {
      this.queryparams.pageSize = data;
      this.getclist();
    },
    handleCurrentChange(data) {
      this.queryparams.pageNum = data;
      this.getclist();
    },
  },

  mounted() {
    this.getclist();
    this.getclistwithchild();
  },
  watch: {
    $route(a, b) {
      console.log("a", a);
      console.log("b", b);
      var nextPath = "/shop/shopcate?parentId=" + this.getselectId;
      if (a.fullPath == "/shop/shopcate") {
        this.queryparams = {
          pageNum: 1,
          pageSize: 5,
          parentId: 0,
        };
        this.getclist(this.queryparams);
        this.panduan = false;
      } else if (a.fullPath == nextPath) {
        this.queryparams = {
          pageNum: 1,
          pageSize: 5,
          parentId: this.getselectId,
        };
        this.getclist(this.queryparams);
        this.panduan = true;
      }
    },
  },
};
</script>

<style scoped='scoped'>
.fenye {
  float: left;
  margin-left: 1090px;
}
.fenye2 {
  float: left;
  margin-left: 1090px;
}
.datalist {
  height: 60px;
  line-height: 60px;
  margin: 20px 20px;
  margin-top: 40px;
  outline: 1px solid #dcdfe6;
  padding-left: 20px;
}
.dataliest-span2 {
  margin-left: 1290px;
}
.biao {
  margin: 20px 20px;
}
.biao2 {
  margin: 20px 20px;
}
</style>
