<template>
  <div>
    <div class="search">
      <div class="bt_search_head">
        <div style="font-size: 16px">
          <i class="el-icon-search"></i>
          筛选搜索
        </div>
        <div>
          <el-button size="small" @click="resetSearch">重置</el-button>
          <el-button size="small" type="primary" @click="seachHandler"
            >查询结果</el-button
          >
        </div>
      </div>
      <el-form :inline="true" :model="queryparams">
        <div class="bt_form_br">
          <el-form-item label="输入搜索:">
            <el-input
              style="width: 208px"
              size="small"
              @keydown.enter.native="seachHandler"
              v-model="queryparams.keyword"
              placeholder="商品名称"
            ></el-input>
          </el-form-item>
          <el-form-item label="商品货号:">
            <el-input
              style="width: 208px"
              size="small"
              v-model="queryparams.productSn"
              placeholder="商品货号"
            ></el-input>
          </el-form-item>
          <el-form-item label="商品分类:">
            <el-cascader
              size="small"
              v-model="value"
              :options="options"
              :props="{ value: 'id', label: 'name' }"
              @change="changeCateHandler"
            >
            </el-cascader>
          </el-form-item>
        </div>
        <div class="bt_form_br">
          <el-form-item size="small" label="商品品牌:">
            <!-- <el-input size="small" v-model="queryparams.keyword" placeholder="选择品牌"></el-input> -->
            <el-select v-model="queryparams.brandId" placeholder="请选择品牌">
              <el-option
                v-for="item in brandList"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          
          <el-form-item size="small" label="上架状态:">
            <!-- <el-input v-model="queryparams.keyword" placeholder="全部"></el-input> -->
            <el-select v-model="queryparams.publishStatus" placeholder="全部">
              <el-option label="上架" value="1"></el-option>
              <el-option label="下架" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="small" label="审核状态:">
            <!-- <el-input v-model="queryparams.keyword" placeholder="全部"></el-input> -->
            <el-select v-model="queryparams.verifyStatus" placeholder="全部">
              <el-option label="审核通过" value="1"></el-option>
              <el-option label="未审核" value="0"></el-option>
            </el-select>
          </el-form-item>
        </div>
      </el-form>
    </div>
    <div class="datalist">
      <span class="dataliest-span1"
        ><i class="el-icon-tickets"></i> 数据列表</span
      >
      <span class="dataliest-span2"
        ><el-button size="small" @click="add">添加</el-button></span
      >
    </div>
    <div class="biao">
      <el-table :data="tableData" border style="width: 100%" @selection-change="handleSelectionChange" ref="multipleTable">
        <el-table-column
          type="selection"
          width="55"
          align="center">
        </el-table-column>
        <el-table-column prop="id" align="center" label="编号" width="100">
        </el-table-column>
        <el-table-column label="商品图片" align="center" width="120">
          <template v-slot="vs">
            <img width="80" height="80" :src="vs.row.pic" />
          </template>
        </el-table-column>
        <el-table-column
          prop="name"
          align="center"
          label="商品名称"
          width="360"
        >
          <template v-slot="vs">
            <p>{{ vs.row.name }}</p>
            <p>品牌：{{ vs.row.brandName }}</p>
          </template>
        </el-table-column>
        <el-table-column prop="id" align="center" label="价格货号" width="129">
          <template v-slot="vs">
            <p>价格：¥{{ vs.row.price }}</p>
            <p>货号：{{ vs.row.productSn }}</p>
          </template>
        </el-table-column>
        <el-table-column align="center" label="标签" width="140">
          <template v-slot="vs">
            <p>
              上架：
              <el-switch
                :active-value='1'
                :inactive-value='0'
                v-model="vs.row.publishStatus"
                active-color="#409eff"
                inactive-color="#dcdfe6"
                @change="handlepublishStatus(vs.row.id,vs.row.publishStatus)"
              >
              </el-switch>
            </p>
            <p>
              新品：
              <el-switch
                :active-value='1'
                :inactive-value='0'
                v-model="vs.row.newStatus"
                active-color="#409eff"
                inactive-color="#dcdfe6"
                @change="handlenewStatus(vs.row.id,vs.row.newStatus)"
              >
              </el-switch>
            </p>
            <p>
              推荐：
              <el-switch
                :active-value='1'
                :inactive-value='0'
                v-model="vs.row.recommandStatus"
                active-color="#409eff"
                inactive-color="#dcdfe6"
                @change="handlerecommendStatus(vs.row.id,vs.row.recommandStatus)"
              >
              </el-switch>
            </p>
          </template>
        </el-table-column>
        <el-table-column prop="sort" align="center" label="排序" width="100">
        </el-table-column>
        <el-table-column align="center" label="库存" width="100">
          <template v-slot="vs">
            <el-button
              type="primary"
              icon="el-icon-edit"
              circle
              @click="getsku(vs.row)"
            ></el-button>
            <!--以下是点击库存-->
            <el-dialog
              :visible.sync="dialogVisible"
              width="40%"
              center
            >
              <span style='font-size:18px'>编辑货品信息</span>
              <p><p style='height:40px'></p>
                商品货号：{{ dialog_productSn }}
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <!--以下是点击库存内的搜索Sku编号-->
                <el-input
                  style="width: 270px"
                  size="small"
                  @keydown.enter.native="searchSku(skudata[0].productId)"
                  v-model="skukeyword"
                  placeholder="按sku编号搜索"
                ></el-input>
                <el-button icon="el-icon-search" size="small" style='background-color:#f5f7fa' @click='searchSku(skudata[0].productId)'></el-button>
              </p>
              <!--以下是点击库存内的内容展示-->
              <el-table :data="skudata" border style="width: 100%;margin-top:20px">
                <el-table-column align="center" label="SKU编号" width="170">
                  <template v-slot='vs'>
                    <el-input v-model='vs.row.skuCode'></el-input>
                  </template>
                </el-table-column>

                <el-table-column align="center" width="90" v-for='(item,index) in attributelist' :label='item.name'>
                  <template v-slot='vs'>
                    <el-input v-model='vs.row.spData[index].value'></el-input>
                  </template>
                </el-table-column>

                <el-table-column align="center" label="销售价格" width="90">
                  <template v-slot='vs'>
                    <el-input v-model='vs.row.price'></el-input>
                  </template>
                </el-table-column>
                <el-table-column  align="center" label="商品库存" width="91">
                  <template v-slot='vs'>
                    <el-input v-model='vs.row.stock' align="center"></el-input>
                  </template>
                </el-table-column>
                <el-table-column align="center" label="库存预警值" width="100"> <!--prop="lockStock"只用来显示
                v-slot2选1-->
                  <template v-slot='vs'>
                    <el-input v-model='vs.row.lockStock'></el-input>
                  </template>
                </el-table-column>
              </el-table>
              <!--以下是sku编辑表的下部俩按钮-->
              <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false;skukeyword=null">取 消</el-button>
                <el-button type="primary" @click='plEditSku(skudata[0].productId)'
                  >确 定</el-button
                >
              </span>
            </el-dialog>
          </template>
        </el-table-column>
        <el-table-column prop="sale" align="center" label="销量" width="100">
        </el-table-column>
        <el-table-column align="center" label="审核状态" width="100">
          <template v-slot="vs">
            <p>未审核</p>
            <p>
              <a href="#" style="text-decoration: none; color: #66b1ff"
                >审核详情</a
              >
            </p>
          </template>
        </el-table-column>
        <el-table-column align="center" label="操作" width="180">
          <template v-slot="vs">
            <p>
              <el-button size="mini">查看</el-button
              ><el-button size="mini" @click='edit'>编辑</el-button>
            </p>
            <p class="gao"></p>
            <p>
              <el-button size="mini">日志</el-button
              ><el-button size="mini" type="danger" @click="dProduct(vs.row.id)"
                >删除</el-button
              >
            </p>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="fenye">
      <el-row>
        <el-col :span='14'>
          <el-select v-model="clickGetStatus" placeholder="请选择品牌" size='small'>
              <el-option
                v-for="item in plcaozuoData"
                :label="item.name"
                :value="item.value"
              ></el-option>
            </el-select>
            <el-button type='primary' size='small' @click='plOption'>确定</el-button>
        </el-col>
        <el-col :span='10'>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="this.queryparams.pageNum"
            :page-sizes="[2, 3, 4, 5]"
            :page-size="5"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
          >
      </el-pagination></el-col>
      </el-row>
      
    </div>
  </div>
</template>

<script>
import { getShoplist } from "../../../../api/shop.js";
import { getPmsCateWithChildren } from "../../../../api/productcate.js";
import { getBrandList } from "../../../../api/productbrand.js";
import { productDelete } from "../../../../api/productdelete.js";
import { productSku } from "../../../../api/productsku.js";
import { updateSku } from "../../../../api/productsku.js";
import { changepublishStatus } from "../../../../api/shop.js";
import { changenewStatus } from "../../../../api/shop.js";
import { changerecommendStatus } from "../../../../api/shop.js";
import { productAttributelist } from "../../../../api/shop.js";
export default {
  data() {
    return {
      attributelist: [],
      isShow: false,
      title: "",
      search_skudata: [],
      skukeyword: null,
      clickGetStatus: [],
      idsArry: "",
      plcaozuoData: [
        { name: "商品上架", value: [1, 1] },
        { name: "商品下架", value: [0, 1] },
        { name: "设为推荐", value: [1, 2] },
        { name: "取消推荐", value: [0, 2] },
        { name: "设为新品", value: [1, 3] },
        { name: "取消新品", value: [0, 3] },
      ],
      dialog_productSn: "",
      spData: [],
      skudata: [],
      dialogVisible: false,
      total: 0,
      tableData: [],
      dtable: {},
      value: [],
      options: [],
      brandList: [], //品牌数据列表
      // checked:'',
      queryparams: {
        pageNum: 1,
        pageSize: 5,
        brandId: null,
        keyword: "",
        productCategoryId: null, // 产品分类id
        productSn: "", //商品货号
        publishStatus: null, //上架状态 0->下架；1->上架
        verifyStatus: null, //审核状态：0->未审核；1->审核通过
        // deleteStatus:null
      },
    };
  },

  methods: {
    //以下是搜索Sku
    searchSku(id) {
      productSku(id, this.skukeyword).then((resp) => {
        this.skudata = resp.data;
        this.skudata.forEach((element) => {
          element.spData = JSON.parse(element.spData);
        });
      });
    },

    plEditSku(id) {
      this.skukeyword = null;
      this.dialogVisible = false;
      // console.log('sku获得的id',id)
      // console.log('修改后的skudata',this.skudata)
      this.skudata.forEach((element) => {
        element.spData = JSON.stringify(element.spData);
        // console.log("修改了的spData", element.spData);
      });
      console.log("修改的skudata", this.skudata);
      updateSku(id, this.skudata);
    },

    //批量操作  确定
    plOption() {
      console.log("clickGetStatus", this.clickGetStatus);
      var status = this.clickGetStatus[0];
      var type = this.clickGetStatus[1];
      if (type == 1) {
        this.handlepublishStatus(this.idsArry, status); //方法复用
      } else if (type == 2) {
        this.handlerecommendStatus(this.idsArry, status);
      } else if (type == 3) {
        this.handlenewStatus(this.idsArry, status);
      }
    },
    //单独操作上下架
    handlepublishStatus(id, status) {
      console.log(id, status);
      changepublishStatus({
        ids: id,
        publishStatus: status,
      }).then(() => {
        this.getslist();
      });
    },
    //单独操作设为新品
    handlenewStatus(id, status) {
      changenewStatus({
        ids: id,
        newStatus: status,
      }).then(() => {
        this.getslist();
      });
    },

    //单独操作推荐商品
    handlerecommendStatus(id, status) {
      changerecommendStatus({
        ids: id,
        recommendStatus: status,
      }).then(() => {
        this.getslist();
      });
    },

    //elment组件 复选框change获取选了哪些
    handleSelectionChange(val) {
      this.multipleSelection = val;
      console.log("val", val);
      var arry = val.map((element) => {
        return element.id;
      });
      console.log("arry", arry);
      this.idsArry = arry.join(",");
      console.log(this.idsArry);
    },

    getsku(row) {
      this.skukeyword='';
      this.spData = [];
      this.dialogVisible = true;
      this.dialog_productSn = row.productSn;
      console.log('productAttributeCategoryId',row.productAttributeCategoryId)

      //发送属性列表请求 sku里的属性循环要用到

        //以下是获得循环次数
      productAttributelist({
        cid: row.productAttributeCategoryId,
        pageNum: null,
        pageSize: null,
        type: 0,
      }).then((resp) => {
        console.log("属性列表resp", resp);
        this.attributelist = resp.data.list;
        console.log("this.attributelist", this.attributelist);
      });
        //以下是获得表内内容的
      productSku(row.id, this.skukeyword).then((resp) => {
        console.log("SKUresp", resp);
        this.skudata = resp.data;
        this.skudata.forEach((element) => {
          element.spData = JSON.parse(element.spData);
          console.log("element.spData", element.spData);
          this.spData.push(element.spData);
        });
        console.log("this.skudata", this.skudata);
        console.log("this.spdata", this.spData);
      });
    },

    getslist() {
      getShoplist(this.queryparams)
        // 带着Size和Num两个参数去请求数据
        .then((resp) => {
          console.log("resp", resp);
          this.tableData = resp.data.list;
          this.total = resp.data.total;
        });
    },

    dProduct(id) {
      this.$confirm("请再次确定操作，是否删除该商品?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "删除成功!",
          }); //删除单个商品
          productDelete({
            deleteStatus: 1,
            ids: id,
          }).then(() => {
            this.getslist();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },

    handleSizeChange(data) {
      this.queryparams.pageSize = data;
      // console.log(`每页 ${data} 条`);
      this.getslist();
    },
    handleCurrentChange(data) {
      this.queryparams.pageNum = data;
      // console.log(`当前页: ${data}`);
      this.getslist();
    },
    changeCateHandler(data) {
      // console.log(data);
      this.queryparams.productCategoryId = data[1];
    },
    seachHandler() {
      this.queryparams.pageSize = 5;
      this.queryparams.pageNum = 1;
      this.getslist();
    },
    resetSearch() {
      this.queryparams = {};
      this.getslist();
      this.$message("已为您重置搜索！");
    },
    add() {
      this.$router.push("/shop/shopadd");
    },
    edit() {
      this.$router.push("/shop/shopedit");
    },
  },

  mounted() {
    this.getslist();
    //获取所有的一级分类以及子分类
    getPmsCateWithChildren().then((data) => {
      // console.log("分类", data);
      this.options = data.data;
    });

    //获取所有的品牌数据
    getBrandList().then((data) => {
      // console.log("branddata", data);
      this.brandList = data.data;
    });
  },
};
</script>

<style scoped='scoped'>
.bt_search_head {
  display: flex;
  justify-content: space-between;
  padding: 0px 20px;
  padding-top: 20px;
}
.el-form {
  padding: 20px 0px;
  margin-top: -20px;
}
.el-form .bt_form_br {
  display: flex;
  padding-left: 50px;
}
.el-form .el-form-item {
  flex: 1;
  padding: 0;
  margin: 0;
}
/* .fenye {
  float: left;
  margin-left: 55%;
} */
.gao {
  height: 10px;
}
.search {
  height: 175px;
  margin: 20px 20px;
  outline: 1px solid #dcdfe6;
}
.datalist {
  height: 60px;
  line-height: 60px;
  margin: 20px 20px;
  outline: 1px solid #dcdfe6;
  padding-left: 20px;
}
.dataliest-span2 {
  margin-left: 1290px;
}
.biao {
  margin: 20px 20px;
}
</style>
