//专门用来放商品的请求
import service from '@/utils/http.js'

export function getBrandList(){
	return service({
		url:"/brand/listAll"
	})
}

export function getBrandName(id){
	return service({
		url:"/brand/"+id
	})
}

export function getBrandDelete(id){
	return service({
		url:"/brand/delete/"+id
	})
}

export function changefactoryStatus(data){
	return service({
		url:"/brand/update/factoryStatus",
		params:{
			ids:data.ids,
			factoryStatus:data.factoryStatus
		},
		method:'POST'
	})
}

export function changeshowStatus(data){
	return service({
		url:"/brand/update/showStatus",
		params:{
			ids:data.ids,
			showStatus:data.showStatus
		},
		method:'POST'
	})
}
