<template>
    <div>
        <div class="form_box">
      <el-form ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <template>
          <div class="change_box">
            <div class="click_box">
              <el-button
                type="primary"
                style="margin-top: 12px; font-size: 14px"
                @click="prev_step"
                size="small"
                >上一步,填写商品属性</el-button
              >
              <el-button
                type="primary"
                style="margin-top: 12px; font-size: 14px"
                @click="next_step"
                size="small"
                >完成,提交商品</el-button
              >
            </div>

            <el-form-item label="关联专题:" style='margin-top:20px'>
              <el-transfer
                filterable
                :filter-method="filterMethod"
                filter-placeholder="请输入专题名称"
                v-model="value"
                :data="data"
                :titles='titles'
                >
              </el-transfer>
            </el-form-item>

            <el-form-item label="关联优选:" style='margin-top:20px'>
              <el-transfer
                filterable
                :filter-method="filterMethod2"
                filter-placeholder="请输入专题名称"
                v-model="value2"
                :data="data2"
                :titles='titles2'
                >
              </el-transfer>
            </el-form-item>
          </div>
        </template>
      </el-form>
    </div>
  </div>
    </div>
</template>

<script>
export default {
  props: ["vs"],
  data() {
    const generateData = (_) => {
      const data = [];
      const cities = [
        "polo衬衫的也时尚",
        "大牌手机低价秒",
        "骁龙845新品上市",
        "夏天应该穿什么",
        "夏季精选",
        "品牌手机降价",
      ];
      const pinyin = [
        "polo衬衫的也时尚",
        "大牌手机低价秒",
        "骁龙845新品上市",
        "夏天应该穿什么",
        "夏季精选",
        "品牌手机降价",
      ];
      cities.forEach((city, index) => {
        data.push({
          label: city,
          key: index,
          pinyin: pinyin[index],
        });
      });
      return data;
    };

    const generateData2 = (_) => {
      const data2 = [];
      const cities2 = [
        "让音质更出众",
        "让音质更出众22",
        "让音质更出众33",
        "让音质更出众44",
      ];
      const pinyin2 = [
        "让音质更出众",
        "让音质更出众22",
        "让音质更出众33",
        "让音质更出众44",
      ];
      cities2.forEach((city2, index) => {
        data2.push({
          label: city2,
          key: index,
          pinyin2: pinyin2[index],
        });
      });
      return data2;
    };
    return {
      info_step:0,
      titles: ["待选择", "已选择"],
      data: generateData(),
      value: [],
      filterMethod(query, item) {
        return item.pinyin.indexOf(query) > -1;
      },

      titles2: ["待选择", "已选择"],
      data2: generateData2(),
      value2: [],
      filterMethod2(query, item) {
        return item.pinyin2.indexOf(query) > -1;
      },
    };
  },
  methods: {
    prev_step() {
      this.info_step = 2;
      this.$emit("trans_infostep", this.info_step);
    },
    next_step() {
      this.info_step = 4;
      this.$emit("trans_infostep", this.info_step);
    },
  },
};
</script>

<style scoped='scoped'>
.click_box {
  position: absolute;
  top: 840px;
  left: 750px;
}
.form_box {
  padding-top: 20px;
  width: 100%;
  height: 630px;
  float: left;
}
</style>
