//专门用来放商品的请求
import service from '@/utils/http.js'

export function productDelete(data){
	return service({
		url:"/product/update/deleteStatus",
		params:data,
		method:'POST'
	})
}

export function productAttributeDelete(id){
	return service({
		url:"/productAttribute/category/delete/"+id,
	})
}

export function typeListDelete(data){
	return service({
		url:"/productAttribute/delete",
		params:{
			ids:data.ids
		},
		method:'POST'
	})
}