<template>
  <div>
    <div class="search">
      <div class="bt_search_head">
        <div style="font-size: 16px">
          <i class="el-icon-search"></i>
          筛选搜索
        </div>
        <div>
          <el-button size="small" type="primary" @click="seachHandler"
            >查询结果</el-button
          >
        </div>
      </div>
      <el-form :inline="true" :model="queryparams">
        <div class="bt_form_br">
          <el-form-item label="输入搜索:">
            <el-input
              style="width: 208px"
              size="small"
              @keydown.enter.native="seachHandler"
              v-model="queryparams.keyword"
              placeholder="品牌名称/关键字"
            ></el-input>
          </el-form-item>
        </div>
        
      </el-form>
    </div>

    <div class="datalist">
      <span class="dataliest-span1"
        ><i class="el-icon-tickets"></i> 数据列表</span
      >
      <span class="dataliest-span2"
        ><el-button size="small" @click="add">添加</el-button></span
      >
    </div>
    <div class="biao">
      <el-table :data="tableData" border style="width: 100%">
        <el-table-column align="center" prop="" label="" width="60">
          <el-checkbox></el-checkbox>
        </el-table-column>

        <el-table-column prop="id" align="center" label="编号" width="100">
        </el-table-column>

        <el-table-column prop="name" align="center" label="品牌名称" width="490">
        </el-table-column>

        <el-table-column prop="firstLetter" align="center" label="品牌首字母" width="100">
        </el-table-column>

        <el-table-column prop="sort" align="center" label="排序" width="100">
        </el-table-column>

        <el-table-column align="center" label="品牌制造商" width="100">
          <template v-slot="vs">
             <el-switch
             :active-value="1"
                :inactive-value="0"
                v-model="vs.row.factoryStatus"
                active-color="#409eff"
                inactive-color="#dcdfe6"
                @change="handlefactoryStatus(vs.row.id,vs.row.factoryStatus)"
              >
              </el-switch>
          </template>
        </el-table-column>

        <el-table-column align="center" label="是否显示" width="100">
          <template v-slot="vs">
             <el-switch
             :active-value="1"
                :inactive-value="0"
                v-model="vs.row.showStatus"
                active-color="#409eff"
                inactive-color="#dcdfe6"
                @change="handleshowStatus(vs.row.id,vs.row.showStatus)"
              >
              </el-switch>
          </template>
        </el-table-column>

        <el-table-column align="center" label="相关" width="220">
          <template v-slot="vs">
            <span>商品：</span><span style='font-size:10px;color:#7dcafc'>{{vs.row.productCount}}</span>&nbsp;&nbsp;&nbsp;
            <span>评论：</span><span style='font-size:10px;color:#7dcafc'>{{vs.row.productCommentCount}}</span>
          </template>
        </el-table-column>

        <el-table-column align="center" label="操作" width="198">
          <template v-slot="vs">
            <p>
              <el-button size="mini">编辑</el-button>
              <el-button size="mini" type="danger" @click="dProduct(vs.row.id)">删除</el-button>
            </p>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="fenye">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="this.queryparams.pageNum"
        :page-sizes="[2, 3, 4, 5]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import {getBrandPageList} from "../../../../api/productbrandfenye.js";
import {getBrandDelete} from "../../../../api/productbrand.js";
import {changefactoryStatus} from "../../../../api/productbrand.js";
import {changeshowStatus} from "../../../../api/productbrand.js";

getBrandDelete
export default {
  data() {
    return {
      total: 0,
      tableData: [],
      dtable: {},
      value: [],
      options: [],
      brandList: [], //品牌数据列表
      // value1: true,
      // value2: true,
      // value3: true,
      // mqueryparams:{
      //     pageNum:1,
      //     pageSize:5,
      // },
      // checked:'',
      queryparams: {
        pageNum: 1,
        pageSize: 5,
        keyword:'',
        firstLetter:''
        // brandId: null,
        // keyword: "",
        // productCategoryId: null, // 产品分类id
        // productSn: "", //商品货号
        // publishStatus: null, //上架状态 0->下架；1->上架
        // verifyStatus: null, //审核状态：0->未审核；1->审核通过
        // deleteStatus:null
      },
    };
  },

  methods: {

    handleshowStatus(ids,showStatus){
      console.log(ids, status);
      changeshowStatus({
        ids: ids,
        showStatus: showStatus,
      }).then(() => {
        this.getctbrandlist();
      });
    },

    handlefactoryStatus(ids,factoryStatus){
      console.log(ids, status);
      changefactoryStatus({
        ids: ids,
        factoryStatus: factoryStatus,
      }).then(() => {
        this.getctbrandlist();
      });
    },

    add(){
      this.$router.push('/shop/shopbrandadd')
    },

    seachHandler() {
      this.queryparams.pageNum = 1;
      this.queryparams.pageSize = 5;
      this.getctbrandlist();
    },

    getctbrandlist() {
      getBrandPageList(this.queryparams).then((resp) => {
          console.log("brandpageresp", resp);
          this.tableData = resp.data.list;
          this.total = resp.data.total;
        });
    },

    dProduct(id) {
      console.log('id',id)
      this.$confirm("请再次确定操作，是否删除该商品?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "删除成功!",
          }); //删除单个商品
          getBrandDelete(id).then(() => {
            this.getctbrandlist();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },

    handleSizeChange(data) {
      this.queryparams.pageSize = data;
      this.getctbrandlist();
    },
    handleCurrentChange(data) {
      this.queryparams.pageNum = data;
      this.getctbrandlist();
    },
    changeCateHandler(data) {
      // console.log(data);
      // this.queryparams.productCategoryId = data[1];
    },
  },

  mounted() {
    
    this.getctbrandlist();
    //获取所有的一级分类以及子分类

    // getPmsCateWithChildren().then((data) => {
    //   // console.log("分类", data);
    //   this.options = data.data;
    // });

    //获取所有的品牌数据
    // getBrandList().then((data) => {
    //   // console.log("branddata", data);
    //   this.brandList = data.data;
    // });
  },
};
</script>

<style scoped='scoped'>
.el-form .bt_form_br {
  display: flex;
  padding-left: 50px;
}
.bt_search_head {
  display: flex;
  justify-content: space-between;
  padding: 0px 20px;
  padding-top: 20px;
}
.search {
  height: 120px;
  margin: 20px 20px;
  outline: 1px solid #dcdfe6;
}
.fenye {
  float: left;
  margin-left: 1050px;
}
.datalist {
  height: 60px;
  line-height: 60px;
  margin: 20px 20px;
  outline: 1px solid #dcdfe6;
  padding-left: 20px;
}
.dataliest-span2 {
  margin-left: 1290px;
}
.biao {
  margin: 20px 20px;
}
</style>
