<template>
  <div class="leftnav">
    <el-menu
      default-active="2"
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      background-color="#304156"
      text-color="#fff"
      active-text-color="#359eff"
      :collapse="isCollapse"
    >
      <router-link to="/home">
        <el-menu-item index="首页">
          <i class="el-icon-s-home"></i>
          <span v-if='!isCollapse'> 首页</span>
        </el-menu-item>
      </router-link>

      <template v-for="item in routelist" v-if="item.meta.isMenu">
        <el-submenu :index="item.meta.name">
          <template slot="title">
            <i :class="item.meta.icon"></i>
            <span> {{ item.meta.name }}</span>
          </template>
          <template v-for="item2 in item.children" v-if="item2.meta.isMenu">
            <div class="beijingse">
              <router-link :to="item.path + '/' + item2.path">
                <el-menu-item :index="item2.meta.name">
                  <i :class="item2.meta.icon"></i>
                  {{ item2.meta.name }}
                </el-menu-item>
              </router-link>
            </div>
          </template>
        </el-submenu>
      </template>

      <template v-for="item in routelist" v-if="item.meta.isOrder">
        <el-submenu :index="item.meta.name">
          <template slot="title">
            <i :class="item.meta.icon"></i>
            <span> {{ item.meta.name }}</span>
          </template>
          <template v-for="item2 in item.children" v-if="item2.meta.isOrder">
            <router-link :to="item.path + '/' + item2.path">
              <el-menu-item :index="item2.meta.name">
                <i :class="item2.meta.icon"></i>
                {{ item2.meta.name }}
              </el-menu-item>
            </router-link>
          </template>
        </el-submenu>
      </template>

      <template v-for="item in routelist" v-if="item.meta.isShell">
        <el-submenu :index="item.meta.name">
          <template slot="title">
            <i :class="item.meta.icon"></i>
            <span> {{ item.meta.name }}</span>
          </template>
          <template v-for="item2 in item.children" v-if="item2.meta.isShell">
            <router-link :to="item.path + '/' + item2.path">
              <el-menu-item :index="item2.meta.name">
                <i :class="item2.meta.icon"></i>
                {{ item2.meta.name }}
              </el-menu-item>
            </router-link>
          </template>
        </el-submenu>
      </template>

      <template v-for="item in routelist" v-if="item.meta.isAdmin">
        <el-submenu :index="item.meta.name">
          <template slot="title">
            <i :class="item.meta.icon"></i>
            <span> {{ item.meta.name }}</span>
          </template>
          <template v-for="item2 in item.children" v-if="item2.meta.isAdmin">
            <router-link :to="item.path + '/' + item2.path">
              <el-menu-item :index="item2.meta.name">
                <i :class="item2.meta.icon"></i>
                {{ item2.meta.name }}
              </el-menu-item>
            </router-link>
          </template>
        </el-submenu>
      </template>
    </el-menu>
  </div>
</template>

<script>
import {mapState} from 'vuex'
export default {
  data() {
    return {
      
      routelist: [],
    };
  },
  mounted() {
    this.routelist = this.$router.options.routes;
    //   console.log(this.$router.options.routes)
    console.log(this.$router);
  },
  computed:{
    ...mapState(['isCollapse'])
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
  },
};
</script>

<style scoped='scoped'>
.el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 180px;
    height:100vh
  }
  .el-menu-vertical-demo {
    height:100vh
  }
.beijingse {
  background-color: #001528;
}
.leftnav {
  font-size: 20px;
}
a {
  text-decoration: none;
  color: white;
}
</style>
