<template>
  <div>
    <div class="form_box">
      <el-form ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <template>
          <div class="change_box">
            <div class="click_box">
              <el-button
                type="primary"
                style="margin-top: 12px; font-size: 14px"
                @click="prev_step"
                size="small"
                >上一步,填写商品信息</el-button
              >
              <el-button
                type="primary"
                style="margin-top: 12px; font-size: 14px"
                @click="next_step"
                size="small"
                >下一步,填写商品属性</el-button
              >
            </div>

            <el-form-item size="small" label="赠送积分:">
              <el-input style="width: 530px" v-model="jifen"></el-input>
            </el-form-item>

            <el-form-item size="small" label="赠送成长值:" width="100px">
              <el-input style="width: 530px" v-model="grow"></el-input>
            </el-form-item>

            <el-form-item size="small" label="积分购买限制:" width="100px">
              <el-input style="width: 530px" v-model="limit"></el-input>
            </el-form-item>

            <el-form-item size="small" label="预告商品:" width="100px">
              <el-switch
                :active-value='1'
                :inactive-value='0'
                v-model="yugao"
                active-color="#409eff"
                inactive-color="#dcdfe6"
              >
              </el-switch>
            </el-form-item>

            <el-form-item size="small" label="商品上架:" width="100px">
              <el-switch
                :active-value='1'
                :inactive-value='0'
                v-model="shangjia"
                active-color="#409eff"
                inactive-color="#dcdfe6"
              >
              </el-switch>
            </el-form-item>

            <el-form-item size="small" label="商品推荐:" width="100px">
              新品&nbsp;&nbsp;&nbsp;
              <el-switch
                :active-value='1'
                :inactive-value='0'
                v-model="xinpin"
                active-color="#409eff"
                inactive-color="#dcdfe6"
              >
              </el-switch>
              &nbsp;&nbsp;推荐&nbsp; &nbsp;
              <el-switch
                :active-value='1'
                :inactive-value='0'
                v-model="tuijian"
                active-color="#409eff"
                inactive-color="#dcdfe6"
              >
              </el-switch>
            </el-form-item>

            <el-form-item size="small" label="服务保证:" width="100px">
              <template>
                <!-- `checked` 为 true 或 false -->
                <el-checkbox-group v-model="checkList">
                <el-checkbox label='无忧退货' value='1'></el-checkbox>
                <el-checkbox label='快速退款' value='2'></el-checkbox>
                <el-checkbox label='免费包邮' value='3'></el-checkbox>
                </el-checkbox-group>
              </template>
            </el-form-item>

            <el-form-item size="small" label="详细页标题:" width="100px">
              <el-input style="width: 530px" v-model='title'></el-input>
            </el-form-item>

            <el-form-item size="small" label="详细页描述:" width="100px">
              <el-input style="width: 530px" v-model='desc'></el-input>
            </el-form-item>

            <el-form-item size="small" label="商品关键字:" width="100px">
              <el-input style="width: 530px" v-model='keyword'></el-input>
            </el-form-item>

            <el-form-item size="small" label="商品备注:" width="100px">
              <el-input style="width: 530px" v-model='note'></el-input>
            </el-form-item>
          </div>
        </template>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  props: ["vs"],
  props: ["productParam"],
  data() {
    return {
      checkList_str:'',
      checkList:[],
      info_step: 0,
      jifen: 0,
      grow: 0,
      limit: 0,
      yugao:0,
      shangjia:0,
      xinpin:0,
      tuijian:0,
      title:'',
      desc:'',
      keyword:'',
      note:''
    };
  },
  methods: {
    prev_step() {
      this.info_step = 0;
      this.$emit("trans_infostep", this.info_step);
    },
    next_step() {
      for(var i=0;i<this.checkList.length;i++){
        if(this.checkList[i]=='无忧退货'){
          this.checkList[i]=1
        }else if(this.checkList[i]=='快速退款'){
          this.checkList[i]=2
        }else if(this.checkList[i]=='免费包邮'){
          this.checkList[i]=3
        }
      }
      this.checkList=this.checkList.toString()
      

      this.info_step = 2;
      this.$emit("trans_infostep", this.info_step);

      this.productParam.giftPoint=this.jifen
      this.productParam.giftGrowth=this.grow
      this.productParam.usePointLimit=this.limit
      this.productParam.previewStatus=this.yugao
      this.productParam.publishStatus=this.shangjia
      this.productParam.newStatus=this.xinpin
      this.productParam.recommandStatus=this.tuijian
      
      //服务保证是字符串
      this.productParam.serviceIds=this.checkList
      this.productParam.detailTitle=this.title
      this.productParam.detailDesc=this.desc
      this.productParam.keywords=this.keyword
      this.productParam.note=this.note
      console.log('this.productParam',this.productParam)
    },
  },
};
</script>

<style scoped='scoped'>
.click_box {
  position: absolute;
  top: 840px;
  left: 750px;
}
.form_box {
  padding-top: 20px;
  width: 100%;
  height: 630px;
  float: left;
}
</style>
