import service from '../utils/http.js'

export function getShoplist(data){
    return service({
        url:'/product/list',
        params:data
    })
}

export function changepublishStatus(data){
    return service({
        url:'/product/update/publishStatus',
        params:data,
        method:'POST'
    })
}

export function changenewStatus(data){
    return service({
        url:'/product/update/newStatus',
        params:data,
        method:'POST'
    })
}

export function changerecommendStatus(data){
    return service({
        url:'/product/update/recommendStatus',
        params:data,
        method:'POST'
    })
}

export function productAttributelist(data){
    return service({
        url:'/productAttribute/list/'+data.cid,
        params:{
            pageNum:data.pageNum,
            pageSize:data.pageSize,
            type:data.type
        },
    })
}

export function productCreate(data){
    return service({
        url:'/product/create',
        data:data,
        method:'POST'
    })
}

