<template>
  <div>
    <div class="datalist">
      <span class="dataliest-span1"><i class="el-icon-tickets"></i> 数据列表</span>
      <span class="dataliest-span2">
        <el-button size="small" @click="add">添加</el-button>
      </span>
    </div>
    <div class="biao">
      <el-table :data="tableData" border style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column
          type="selection"
          width="60"
          align="center">
        </el-table-column>

        <el-table-column prop="id" align="center" label="编号" width="100">
        </el-table-column>

        <el-table-column
          prop="name"
          align="center"
          label="属性名称"
          width="140"
        >
        </el-table-column>

        <el-table-column
          align="center"
          label="商品类型"
          width="140"
        >
        {{typename}}
        </el-table-column>

        <el-table-column
          prop="paramCount"
          align="center"
          label="属性是否可选"
          width="120"
        >
        </el-table-column>

        <el-table-column
          align="center"
          label="属性值的录入方式"
          width="150"
        >
          <template v-slot="vs">
            {{vs.row.inputType==0? '手工录入':'从列表中选取'}}
          </template>
        </el-table-column>

        <el-table-column
          align="center"
          label="可选值列表"
          width="475"
        >
          <template v-slot="vs">
            {{vs.row.inputList}}
          </template>
        </el-table-column>

        <el-table-column align="center" label="排序" width="100">
          <template v-slot="vs">
            {{vs.row.sort}}
          </template>
        </el-table-column>

        <el-table-column align="center" label="操作" width="184">
          <template v-slot="vs">
            <p>
              <el-button size="mini" @click='edit'>编辑</el-button>
              <el-button size="mini" type="danger" @click="dProduct(vs.row.id)"
                >删除</el-button
              >
            </p>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="fenye">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="this.queryparams.pageNum"
        :page-sizes="[2, 3, 4, 5]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { typeListDelete } from "../../../../api/productdelete.js";
import { productAttributelist } from "../../../../api/shop";
import { productAttributeCategoryCreate } from "../../../../api/productcateAttr";

export default {
  data() {
    return {
      addTypeName:'',
      inputType:0,
      typename:'',
      query: {},
      dialogVisible: false,
      total: 0,
      tableData: [],
      dtable: {},
      value: [],
      options: [],
      brandList: [], //品牌数据列表
      // value1: true,
      // value2: true,
      // value3: true,
      // mqueryparams:{
      //     pageNum:1,
      //     pageSize:5,
      // },
      // checked:'',
      queryparams: {
        pageNum: 1,
        pageSize: 5,
        // brandId: null,
        // keyword: "",
        // productCategoryId: null, // 产品分类id
        // productSn: "", //商品货号
        // publishStatus: null, //上架状态 0->下架；1->上架
        // verifyStatus: null, //审核状态：0->未审核；1->审核通过
        // deleteStatus:null
      },
    };
  },

  methods: {
    dialogqx(){
      this.dialogVisible = false
      this.addTypeName=''
    },

    dialogqd(){
      console.log('this.addTypeName',this.addTypeName)
      this.dialogVisible = false
      productAttributeCategoryCreate({
        name:this.addTypeName
      }).then(()=>{
        this.addTypeName=''
      })
    },

    handleSelectionChange(){},

    add() {
      this.dialogVisible = true;
      this.$router.push('/shop/shoptypelistadd')
    },

    edit(){
      this.$router.push('/shop/shoptypelistedit')
    },

    getsx() {
      productAttributelist({
        cid: this.query.cid,
        pageNum: this.queryparams.pageNum,
        pageSize: this.queryparams.pageSize,
        type: this.query.type,
      }).then((resp) => {
        this.tableData = resp.data.list;
        console.log("this.tableData", this.tableData);
      });
    },

    dProduct(id) {
      console.log("id", id);
      this.$confirm("请再次确定操作，是否删除该商品?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "删除成功!",
          });
          typeListDelete({
            ids: id,
          }).then(() => {
            this.getsx();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },

    handleSizeChange(data) {
      this.queryparams.pageSize = data;
      this.getsx();
    },
    handleCurrentChange(data) {
      this.queryparams.pageNum = data;
      this.getsx();
    },
  },

  mounted() {
    this.typename=this.$router.history.current.query.cname
    this.query = this.$router.history.current.query;
    console.log("this.query", this.query);
    this.getsx();
  },
};
</script>

<style scoped='scoped'>
.bt_search_head {
  display: flex;
  justify-content: space-between;
  padding: 0px 20px;
  padding-top: 20px;
}
.search {
  height: 175px;
  margin: 20px 20px;
  outline: 1px solid #dcdfe6;
}
.fenye {
  float: left;
  margin-left: 1000px;
}
.datalist {
  height: 60px;
  line-height: 60px;
  margin: 20px 20px;
  outline: 1px solid #dcdfe6;
  padding-left: 20px;
}
.dataliest-span2 {
  margin-left: 1290px;
}
.biao {
  margin: 20px 20px;
}
</style>
