<template>
  <div>
    <div class="show_box">
      <div class="form_box">
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <template>
            <div class="change_box">
              <div class="click_box">
                <el-button
                  type="primary"
                  style="margin-top: 12px; font-size: 14px"
                  @click="Addimit"
                  size="small"
                  >提交</el-button
                >
                <el-button
                  style="margin-top: 12px; font-size: 14px"
                  @click="next_step(ruleForm)"
                  size="small"
                  >重置</el-button
                >
              </div>
              <el-form-item
                size="small"
                label="分类名称:"
                prop="catename"
                style="width: 700px"
              >
                <el-input v-model="ruleForm.catename"></el-input>
              </el-form-item>

              <el-form-item size="small" label="上级分类:" prop="fenlei">
                <el-select
                  placeholder="请选择"
                  v-model="getId"
                  @change="selectshangjifenlei"
                >
                  <el-option
                    v-for="val in options"
                    :label="val.name"
                    :value="val.id"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item
                size="small"
                label="数量单位:"
                width="100px"
                style="width: 700px"
              >
                <el-input v-model="ruleForm.danwei"></el-input>
              </el-form-item>

              <el-form-item
                size="small"
                label="排序:"
                prop="name2"
                width="100px"
                style="width: 700px"
              >
                <el-input v-model="ruleForm.paixu">0</el-input>
              </el-form-item>

              <el-form-item
                size="small"
                label="是否显示:"
                prop="name2"
                width="100px"
                style="width: 700px"
              >
                <el-radio-group v-model="radio1">
                  <el-radio :label="1">是</el-radio>
                  <el-radio :label="0">否</el-radio>
                </el-radio-group>
              </el-form-item>

              <el-form-item
                size="small"
                label="是否显示在导航栏:"
                prop="name2"
                width="100px"
                style="width: 700px"
              >
                <el-radio-group v-model="radio2">
                  <el-radio :label="1">是</el-radio>
                  <el-radio :label="0">否</el-radio>
                </el-radio-group>
              </el-form-item>

              <el-form-item
                size="small"
                label="分类图标:"
                prop="name2"
                width="100px"
                style="width: 700px"
              >
                <el-upload
                  class="upload-demo"
                  action="https://jsonplaceholder.typicode.com/posts/"
                  :on-preview="handlePreview"
                  :on-remove="handleRemove"
                  :before-remove="beforeRemove"
                  multiple
                  :limit="3"
                  :on-exceed="handleExceed"
                  :file-list="fileList"
                >
                  <el-button size="small" type="primary">点击上传</el-button>
                  <div slot="tip" class="el-upload__tip">
                    只能上传jpg/png文件，且不超过10mb
                  </div>
                </el-upload>
              </el-form-item>

              <el-form-item
                size="small"
                label="筛选属性:"
                prop="name2"
                width="100px"
                style="width: 700px"
              >
                <el-cascader
                  size="small"
                  v-model="selectAttr"
                  :options="options2"
                  @change="selectAttrChange"
                  :props="{
                    value: 'id',
                    label: 'name',
                    children: 'productAttributeList',
                  }"
                  placeholder="请选择"
                >
                </el-cascader>
                &nbsp;&nbsp;<el-button size="small">删除</el-button>
                <p style="margin-top: 20px">
                  <el-button size="small" type="primary">编辑</el-button>
                </p>
              </el-form-item>

              <el-form-item
                size="small"
                label="关键词:"
                width="100px"
                style="width: 700px"
              >
                <el-input v-model="ruleForm.name"></el-input>
              </el-form-item>

              <el-form-item size="small" label="分类描述:" style="width: 700px">
                <el-input
                  type="textarea"
                  v-model="ruleForm.desc"
                  placeholder=""
                ></el-input>
              </el-form-item>
            </div>
          </template>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import { getPmsCateWithChildren } from "../../../../api/productcate.js";
import { getPmsCatewithAttr } from "../../../../api/productcateAttr.js";
import { productCateAdd } from "../../../../api/productCateDelete.js";
export default {
  props: ["vs"],
  data() {
    return {
      fileList: [],
      selectAttr: "",
      getId: "",
      radio1: 0,
      radio2: 0,
      info_step: 1,
      options: [],
      options2: [],
      brandList: [],
      productCategoryParam: [],
      ruleForm: {
        catename: "",
        name2: "",
        fenlei: "",
        pinpai: "",
        desc: "",
        price: 0,
        markPrice: 0,
        kucun: 0,
        danwei: "",
        weight: 0,
        paixu: 0,
      },
      rules: {
        catename: [
          { required: true, message: "请输入分类名称", trigger: "blur" },
          { min: 1, max: 10, message: "长度在 3 到 5 个字符", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    Addimit() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.$confirm("请再次确定操作，是否提交?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "提交成功!",
          }); 
          var aa = {
        description: "",
        icon: "",
        keywords: "",
        name: this.ruleForm.catename,
        navStatus: 0,
        parentId: 0,
        productAttributeIdList: [],
        productUnit: "",
        showStatus: 0,
        sort: 0,
      };
      console.log('aa',aa)
      productCateAdd(aa);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消提交",
          });
        });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
      
    },
    // Addimit() {
    //   var aa = {
    //     description: "",
    //     icon: "",
    //     keywords: "",
    //     name: this.ruleForm.catename,
    //     navStatus: 0,
    //     parentId: 0,
    //     productAttributeIdList: [],
    //     productUnit: "",
    //     showStatus: 0,
    //     sort: 0,
    //   };
    //   console.log('aa',aa)
    //   productCateAdd(aa);
    // },

    selectAttrChange(data) {
      console.log("selectAttrChange", data);
    },
    selectshangjifenlei() {},
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${
          files.length + fileList.length
        } 个文件`
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
    next_step(ruleForm) {
      console.log(this.$refs);
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.$emit("trans_infostep", this.info_step);
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
  },
  mounted() {
    getPmsCatewithAttr().then((resp) => {
      console.log("getPmsCatewithAttr", resp);
      this.options2 = resp.data;
      console.log("this.options2", this.options2);
      this.options2.forEach((item) => {});
    });

    getPmsCateWithChildren().then((data) => {
      console.log("分类", data);
      this.options = data.data;
    });
  },
};
</script>
<style scoped='scoped'>
.show_box {
  outline: 1px solid #ebeef5;
  width: 800px;
  height: 871.67px;
  margin: 20px 310px;
  padding: 40px 40px 20px;
}
.danwei {
  outline: 1px solid red;
  width: 20px;
}
.click_box {
  position: absolute;
  top: 840px;
  left: 860px;
}
.form_box {
  padding-top: 20px;
  width: 100%;
  height: 630px;
  float: left;
}
</style>
