<template>
  <div>
    <div class="bread">
      <img src="../neirong/pics/hx.png" id="hx" @click='roll'>
      <template v-if="$route.path == '/home'">
        <span class="shouye1" style='margin-left:40px'>首页</span>
      </template>
      <template v-else>
        <span class="shouye2" style='margin-left:40px'>
          <router-link to="/home">
        首页
        </router-link>
        </span>
      </template>
      <span class="fh">{{ fuhao1 }}</span>
      <span @click="fuTiao" class="fu">{{ fu }}</span>
      <span class="fh">{{ fuhao2 }}</span>
      <span class="zi">{{ zi }}</span>
    </div>

    <div class="select">
      <el-dropdown @command="handleCommand" trigger="click">
        <span class="el-dropdown-link">
          <img src="@/assets/timg.jpg" class="apple" />
          <i class="el-icon-caret-bottom"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="toshouye">首页</el-dropdown-item>
          <el-dropdown-item command="quiet">退出</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
import Cookies from "js-cookie"
import {mapMutations} from 'vuex'
// import {SETWIDTH} from '../../../store/mutations.js'
import {SETCOllAPSE} from '../../../store/mutations.js'
export default {
  data() {
    return {
      changeisCollapse:false,
      changewidth:-130,
      deg:0,
      zi: "",
      fu: "",
      fuhao1: "",
      fuhao2: "",
    };
  },
  mounted() {
    // console.log("this", this.width);
    // console.log(this.$store.state.width)
    // console.log(this.$router.history.current.meta.name);
  },
  computed:{
    // ...mapState(['width']),
    // ...mapMutations([SETWIDTH]),
    ...mapMutations([SETCOllAPSE]),
  },
  watch: {
    $route(a, b) {
      // console.log("a", a);
      this.zi = a.meta.name;
      // console.log("最新名字", a.meta.name);
      this.fu = a.matched[0].meta.name;
      if (a.meta.name == undefined) {
        this.fuhao1 = "";
        this.fuhao2 = "";
      } else {
        this.fuhao1 = " / ";
        this.fuhao2 = " / ";
      }
      // console.log("b", b);
    },
  },
  methods: {
    

    roll(){
      this.deg+=90
      document.getElementById("hx").style.transform = "rotate(" + this.deg + "deg)";
      // this.$store.commit(SETWIDTH,{name:"width",value:this.changewidth})
      this.$store.commit(SETCOllAPSE,{name:"isCollapse",value:this.changeisCollapse})
      // this[SETWIDTH]({name:"width",value:this.changewidth})
      this.changewidth=-this.changewidth
      this.changeisCollapse=!this.changeisCollapse
      // console.log('this.changewidth',this.changewidth)
      console.log('this.changeisCollapse',this.changeisCollapse)
      console.log('this',this)
    },

    handleCommand(command) {
      if (
        command == "toshouye" &&
        this.$router.history.current.path !== "/home"
      ) {
        // console.log("222222", this.$router.history.current.path);
        // console.log(typeof this.$router.history.current.path);
        // console.log(this.$router);
        this.$router.push("/home");
        this.$message("已返回首页！");
      } else if (
        command == "toshouye" &&
        this.$router.history.current.path == "/home"
      ) {
        this.$message("傻逼吗？这他妈的就是首页，点你麻痹呢？？");
      } else {
        Cookies.remove("token");
        this.$router.push("/login");
        this.$message("已退出登录！");
      }
    },
    fuTiao() {
      this.$router.push(this.$router.history.current.matched[0].path);
      // console.log(this.$router.history.current.matched[0].path);
    },
  },
};
</script>

<style scoped='scoped'>
#hx {
  width: 20px;
  height: 20px;
  position: absolute;
  top:16px;
  transition-duration: 0.5s;
}

.bread {
  margin-left: 12px;
}
.fh {
  font-size: 10px;
  color: #97a8be;
}
.fu {
  font-size: 14px;
  font-weight: bold;
}
.zi {
  color: #97a8be;
  font-size: 14px;
}
.shouye1 {
  color: #97a8be;
  font-size: 14px;
}
.shouye2 a:hover {
  color: #409eff;
}
.shouye2 a {
  text-decoration: none;
  color: black;
  font-weight: bold;
  font-size: 14px;
}
.fu:hover {
  cursor: pointer;
  color: #409eff;
}
.el-icon-caret-bottom {
  position: absolute;
  left: 45px;
}
.apple:hover {
  cursor: pointer;
}
.el-icon-caret-bottom:hover {
  cursor: pointer;
}
.apple {
  width: 40px;
  height: 40px;
  margin-top: -27px;
  position: absolute;
  border-radius: 10px;
}

.select {
  width: 90px;
  height: 50px;
  position: absolute;
  top: 0;
  right: -20px;
}
</style>
