//专门用来放商品的请求
import service from '@/utils/http.js'

export function getPmsCateAttr(data){
	return service({
		url:"/productAttribute/category/list",
		params:{
			pageNum:data.pageNum,
			pageSize:data.pageSize
		}
	})
}

export function getPmsCatewithAttr(){
	return service({
		url:"/productAttribute/category/list/withAttr"
	})
}

export function productAttributeCategoryCreate(data){
	return service({
		url:"/productAttribute/category/create",
		params:{
			name:data.name
		},
		method:'POST'
	})
}

export function productAttributeCategoryUpdate(data){
	return service({
		url:"/productAttribute/category/update/"+data.id,
		params:{
			name:data.name
		},
		method:'POST'
	})
}

