//专门用来放商品的请求
import service from '@/utils/http.js'

export function productCateDelete(id){
	return service({
		url:"/productCategory/delete/"+id,
		method:'POST'
	})
}

export function productCateAdd(data){
	return service({
		url:"/productCategory/create",
		data:data,
		method:'POST'
	})
}
