//专门用来放商品的请求
import service from '@/utils/http.js'

export function productSku(pid,data){
	return service({
		url:"/sku/"+pid,
		params:{
			keyword:data//此处注意对应
		},
	})
}

export function updateSku(pid,data){
	return service({
		url:"/sku/update/"+pid,
		data:data,
		method:'POST'
	})
}
