<template>
  <div>
    <div class="show_box">
      <div class="form_box">
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          class="demo-ruleForm"
        >
          <template>
            <div class="change_box">
              <div class="click_box">
                <el-button
                  type="primary"
                  style="margin-top: 12px; font-size: 14px"
                  @click="next_step(ruleForm)"
                  size="small"
                  >提交</el-button
                >
                <el-button @click="resetForm('ruleForm')">重置</el-button>
              </div>
              <el-form-item
                size="small"
                label="属性名称"
                prop="shuxingName"
                style="width: 700px"
              >
                <el-input
                  style="width: 270px"
                  size="small"
                  v-model="ruleForm.shuxingName"
                ></el-input>
              </el-form-item>

              <el-form-item size="small" label="商品类型：">
                <el-select
                  placeholder="请选择"
                  v-model="selectcateId"
                  @change="selectOption"
                >
                  <el-option
                    v-for="item in cateAttrList"
                    :label="item.name"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>

              <el-form-item
                size="small"
                label="商品名称"
                prop="name"
                style="width: 700px"
              >
                <el-input v-model="ruleForm.name"></el-input>
              </el-form-item>

              <el-form-item
                size="small"
                label="副标题"
                prop="name2"
                width="100px"
                style="width: 700px"
              >
                <el-input v-model="ruleForm.name2"></el-input>
              </el-form-item>

              

              <el-form-item size="small" label="商品介绍" style="width: 700px">
                <el-input
                  type="textarea"
                  v-model="ruleForm.desc"
                  placeholder="请输入内容"
                ></el-input>
              </el-form-item>

              <el-form-item
                size="small"
                label="商品货号"
                width="100px"
                style="width: 700px"
              >
                <el-input v-model="ruleForm.name"></el-input>
              </el-form-item>

              <el-form-item
                size="small"
                label="商品售价"
                width="100px"
                style="width: 700px"
              >
                <el-input v-model="ruleForm.price"></el-input>
              </el-form-item>
              <el-form-item
                size="small"
                label="市场价"
                width="100px"
                style="width: 700px"
              >
                <el-input v-model="ruleForm.markPrice"></el-input>
              </el-form-item>
              <el-form-item
                size="small"
                label="商品库存"
                width="100px"
                style="width: 700px"
              >
                <el-input v-model="ruleForm.kucun"></el-input>
              </el-form-item>

              <el-form-item
                size="small"
                label="计量单位"
                width="100px"
                style="width: 700px"
              >
                <el-input v-model="ruleForm.danwei"></el-input>
              </el-form-item>

              <el-form-item size="small" label="商品重量" width="100px">
                <el-input
                  v-model="ruleForm.weight"
                  style="width: 300px"
                ></el-input>
                &nbsp;&nbsp;&nbsp;克
              </el-form-item>

              <el-form-item
                size="small"
                label="排序"
                width="100px"
                style="width: 700px"
              >
                <el-input v-model="ruleForm.paixu"></el-input>
              </el-form-item>
            </div>
          </template>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import { getPmsCatewithAttr } from "../../../../api/productcateAttr.js";
export default {
  props: ["vs"],
  data() {
    return {
      cateAttrList: [],
      selectcateId:'',
      ruleForm: {
        shuxingName: "",
      },
      rules: {
        shuxingName: [
          { required: true, message: "请输入属性名称", trigger: "blur" },
          { min: 1, max: 8, message: "长度在 1 到 8 个字符", trigger: "blur" },
        ],
      },
    };
  },
  methods: {
    next_step(ruleForm) {
      console.log(this.$refs);
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          //提交 发送请求
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
  mounted() {
    //获得商品类型
    getPmsCatewithAttr().then((resp) => {
      console.log("getPmsCatewithAttr", resp);
      this.cateAttrList = resp.data;
      console.log("this.cateAttrList", this.cateAttrList);
    });
  },
};
</script>
<style scoped='scoped'>
.show_box {
  outline: 1px solid #ebeef5;
  width: 800px;
  height: 871.67px;
  margin: 20px 310px;
  padding: 40px 40px 20px;
}
.danwei {
  outline: 1px solid red;
  width: 20px;
}
.click_box {
  position: absolute;
  top: 840px;
  left: 860px;
}
.form_box {
  padding-top: 20px;
  width: 100%;
  height: 630px;
  float: left;
}
</style>
