<template>
  <div>
    <div class="datalist">
      <span class="dataliest-span1"><i class="el-icon-tickets"></i> 数据列表</span>
      <span class="dataliest-span2">
        
            <el-button size="small" @click="add">添加</el-button>
            <el-dialog
              :visible.sync="dialogVisible"
              width="30%"
              center
            >
            
              <span style='font-size:18px;font-weight:bold'>添加类型</span>
              
                <el-form
              :model="ruleForm"
              :rules="rules"
              ref="ruleForm"
              class="demo-ruleForm"
            >
              <el-form-item size='small' label='类型名称' prop='addTypeName'>
                  <el-input
                    style="width: 270px"
                    size="small"
                    v-model="ruleForm.addTypeName"
                  ></el-input>
              </el-form-item>
                </el-form>
              <span slot="footer" class="dialog-footer">
                <el-button @click="dialogqx">取 消</el-button>
                <el-button type="primary" @click="dialogqd(ruleForm)"
                  >确 定</el-button
                >
              </span>
            </el-dialog>

            <el-dialog
              :visible.sync="dialogVisible2"
              width="30%"
              center
            >
              <span style='font-size:18px;font-weight:bold'>添加类型</span>
                <el-form
              :model="ruleForm2"
              :rules="rules2"
              ref="ruleForm2"
              class="demo-ruleForm2"
            >
              <el-form-item size='small' label='类型名称' prop='addTypeName2'>
                  <el-input
                    style="width: 270px"
                    size="small"
                    v-model="ruleForm2.addTypeName2"
                  ></el-input>
              </el-form-item>
                </el-form>
              <span slot="footer" class="dialog-footer2">
                <el-button @click="dialogqx2">取 消</el-button>
                <el-button type="primary" @click="dialogqd2(ruleForm2)"
                  >确 定</el-button
                >
              </span>
            </el-dialog>
      </span>
    </div>
    <div class="biao">
      <el-table :data="tableData" border style="width: 100%">

        <el-table-column prop="id" align="center" label="编号" width="100">
        </el-table-column>

        <el-table-column
          prop="name"
          align="center"
          label="类型名称"
          width="585"
        >
        </el-table-column>

        <el-table-column
          prop="attributeCount"
          align="center"
          label="属性数量"
          width="200"
        >
        </el-table-column>

        <el-table-column
          prop="paramCount"
          align="center"
          label="参数数量"
          width="200"
        >
        </el-table-column>

        <el-table-column align="center" label="设置" width="200">
          <template v-slot="vs">
            <el-button size="mini" @click='shuxingList(vs.row.id,vs.row.name)'>属性列表</el-button>
            <el-button size="mini" @click='canshuList(vs.row.id,vs.row.name)'>参数列表</el-button>
          </template>
        </el-table-column>

        <el-table-column align="center" label="操作" width="184">
          <template v-slot="vs">
            <p>
              <el-button size="mini" @click='edit(vs.row)'>编辑</el-button>
              <el-button size="mini" type="danger" @click="dProduct(vs.row.id)"
                >删除</el-button
              >
            </p>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="fenye">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="this.queryparams.pageNum"
        :page-sizes="[2, 3, 4, 5]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { getPmsCateAttr } from "../../../../api/productcateAttr.js";
import { productAttributeDelete } from "../../../../api/productdelete.js";
import { productAttributeCategoryCreate } from "../../../../api/productcateAttr";
import { productAttributeCategoryUpdate } from "../../../../api/productcateAttr";
export default {
  data() {
    return {
      clickId: "",
      dialogVisible: false,
      dialogVisible2: false,
      total: 0,
      tableData: [],
      dtable: {},
      value: [],
      options: [],
      brandList: [], //品牌数据列表
      // value1: true,
      // value2: true,
      // value3: true,
      // mqueryparams:{
      //     pageNum:1,
      //     pageSize:5,
      // },
      // checked:'',
      queryparams: {
        pageNum: 1,
        pageSize: 5,
        // brandId: null,
        // keyword: "",
        // productCategoryId: null, // 产品分类id
        // productSn: "", //商品货号
        // publishStatus: null, //上架状态 0->下架；1->上架
        // verifyStatus: null, //审核状态：0->未审核；1->审核通过
        // deleteStatus:null
      },
      ruleForm: {
        addTypeName: "",
      },
      rules: {
        addTypeName: [
          { required: true, message: "请输入类型名称", trigger: "blur" },
          {
            min: 1,
            max: 10,
            message: "长度在 1 到 10 个字符",
            trigger: "blur",
          },
        ],
      },
      ruleForm2: {
        addTypeName: "",
      },
      rules2: {
        addTypeName2: [
          { required: true, message: "请输入类型名称", trigger: "blur" },
          {
            min: 1,
            max: 10,
            message: "长度在 1 到 10 个字符",
            trigger: "blur",
          },
        ],
      },
    };
  },

  methods: {
    dialogqx() {
      this.dialogVisible = false;
      this.ruleForm.addTypeName = "";
    },

    dialogqd(ruleForm) {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          console.log("this.addTypeName", this.addTypeName);
          this.dialogVisible = false;
          productAttributeCategoryCreate({
            name: this.ruleForm.addTypeName,
          }).then(() => {
            this.ruleForm.addTypeName = "";
            this.getctattr();
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    dialogqx2() {
      this.dialogVisible2 = false;
      this.ruleForm2.addTypeName2 = "";
    },

    dialogqd2(ruleForm2) {
      this.$refs.ruleForm2.validate((valid) => {
        if (valid) {
          this.dialogVisible2 = false;
          console.log(this.clickId, this.clickName);
          productAttributeCategoryUpdate({
            id: this.clickId,
            name: this.ruleForm2.addTypeName2,
          }).then(() => {
            this.ruleForm2.addTypeName2 = "";
            this.getctattr();
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },

    //点击 属性列表(弄个新路由，table表头不同，然后路由传参，传type:0或者type:1来给新页面获取属性列表和参数列表)
    shuxingList(id, name) {
      this.$router.push({
        path: "/shop/shoptypelist",
        query: {
          cid: id,
          cname: name,
          type: 0,
        },
      });
    },

    canshuList(id, name) {
      this.$router.push({
        path: "/shop/shoptypelist",
        query: {
          cid: id,
          cname: name,
          type: 1,
        },
      });
    },

    add() {
      this.dialogVisible = true;
    },

    edit(row) {
      // console.log(row.id,row.name)
      this.ruleForm2.addTypeName2 = row.name;
      this.dialogVisible2 = true;
      this.clickId = row.id;
      this.clickName = row.name;
    },

    getctattr() {
      getPmsCateAttr(this.queryparams).then((resp) => {
        console.log("resp", resp);
        this.tableData = resp.data.list;
        console.log("111", this.tableData);
        this.total = resp.data.total;
      });
    },

    dProduct(id) {
      console.log("id", id);
      this.$confirm("请再次确定操作，是否删除该商品?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "删除成功!",
          });
          productAttributeDelete(id).then(() => {
            this.getctattr();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },

    handleSizeChange(data) {
      this.queryparams.pageSize = data;
      this.getctattr();
    },
    handleCurrentChange(data) {
      this.queryparams.pageNum = data;
      this.getctattr();
    },
    // changeCateHandler(data) {
    // console.log(data);
    // this.queryparams.productCategoryId = data[1];
    // },
  },

  mounted() {
    this.getctattr();
    //获取所有的一级分类以及子分类

    // getPmsCateWithChildren().then((data) => {
    //   // console.log("分类", data);
    //   this.options = data.data;
    // });

    //获取所有的品牌数据

    // getBrandList().then((data) => {
    //   // console.log("branddata", data);
    //   this.brandList = data.data;
    // });

    // productverifyStatusi()
    // .then(data=>{
    //   console.log('verti',data)
    // })
  },
};
</script>

<style scoped='scoped'>
.bt_search_head {
  display: flex;
  justify-content: space-between;
  padding: 0px 20px;
  padding-top: 20px;
}
.search {
  height: 175px;
  margin: 20px 20px;
  outline: 1px solid #dcdfe6;
}
.fenye {
  float: left;
  margin-left: 1000px;
}
.datalist {
  height: 60px;
  line-height: 60px;
  margin: 20px 20px;
  outline: 1px solid #dcdfe6;
  padding-left: 20px;
}
.dataliest-span2 {
  margin-left: 1290px;
}
.biao {
  margin: 20px 20px;
}
</style>
