//专门用来放商品的请求
import service from '@/utils/http.js'

export function getPmsCateWithChildren(){
	return service({
		url:"/productCategory/list/withChildren"
	})
}

export function changenavStatus(data){
	return service({
		url:"/productCategory/update/navStatus",
		params:{
			ids:data.ids,
			navStatus:data.navStatus
		},
		method:'POST'
	})
}
export function changeshowStatus(data){
	return service({
		url:"/productCategory/update/showStatus",
		params:{
			ids:data.ids,
			showStatus:data.showStatus
		},
		method:'POST'
	})
}