<template>
  <div>
    <div class='show_box'>
    <div class="form_box">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <template>
          <div class="change_box">
            <div class="click_box">
              <el-button
                type="primary"
                style="margin-top: 12px; font-size: 14px"
                @click="next_step(ruleForm)"
                size="small"
                >下一步,填写商品促销</el-button
              >
            </div>
            <el-form-item size="small" label="商品分类" prop="fenlei">
              <el-cascader
                size="small"
                v-model="ruleForm.fenlei"
                :options="options"
                :props="{ value: 'id', label: 'name' }"
                placeholder="请选择"
              >
              </el-cascader>
            </el-form-item>

            <el-form-item
              size="small"
              label="商品名称"
              prop="name"
              style="width: 700px"
            >
              <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>

            <el-form-item
              size="small"
              label="副标题"
              prop="name2"
              width="100px"
              style="width: 700px"
            >
              <el-input v-model="ruleForm.name2"></el-input>
            </el-form-item>

            <el-form-item size="small" label="商品品牌" prop="pinpai">
              <el-select v-model="ruleForm.pinpai" placeholder="请选择">
                <el-option
                  v-for="item in brandList"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>

            <el-form-item size="small" label="商品介绍" style="width: 700px">
              <el-input
                type="textarea"
                v-model="ruleForm.desc"
                placeholder="请输入内容"
              ></el-input>
            </el-form-item>

            <el-form-item
              size="small"
              label="商品货号"
              width="100px"
              style="width: 700px"
            >
              <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>

            <el-form-item
              size="small"
              label="商品售价"
              width="100px"
              style="width: 700px"
            >
              <el-input v-model="ruleForm.price"></el-input>
            </el-form-item>
            <el-form-item
              size="small"
              label="市场价"
              width="100px"
              style="width: 700px"
            >
              <el-input v-model="ruleForm.markPrice"></el-input>
            </el-form-item>
            <el-form-item
              size="small"
              label="商品库存"
              width="100px"
              style="width: 700px"
            >
              <el-input v-model="ruleForm.kucun"></el-input>
            </el-form-item>

            <el-form-item
              size="small"
              label="计量单位"
              width="100px"
              style="width: 700px"
            >
              <el-input v-model="ruleForm.danwei"></el-input>
            </el-form-item>

            <el-form-item size="small" label="商品重量" width="100px">
              <el-input
                v-model="ruleForm.weight"
                style="width: 300px"
              ></el-input>
              &nbsp;&nbsp;&nbsp;克
            </el-form-item>

            <el-form-item
              size="small"
              label="排序"
              width="100px"
              style="width: 700px"
            >
              <el-input v-model="ruleForm.paixu"></el-input>
            </el-form-item>
          </div>
        </template>
      </el-form>
    </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["vs"],
  data() {
    return {
      info_step: 1,
      options: [],
      brandList: [],
      ruleForm: {
        name: "",
        name2: "",
        fenlei: "",
        pinpai: "",
        desc: "",
        price: 0,
        markPrice: 0,
        kucun: 0,
        danwei: "",
        weight: 0,
        paixu: 0,
      },
      rules: {
        name: [
          { required: true, message: "请输入商品名称", trigger: "blur" },
          { min: 1, max: 10, message: "长度在 3 到 5 个字符", trigger: "blur" },
        ],
        name2: [
          { required: true, message: "请输入副标题", trigger: "blur" },
          { min: 1, max: 10, message: "长度在 3 到 5 个字符", trigger: "blur" },
        ],
        fenlei: [
          { required: true, message: "请选择商品分类", trigger: "change" },
        ],
        pinpai: [
          { required: true, message: "请选择商品品牌", trigger: "change" },
        ],
      },
    };
  },
  methods: {
    next_step(ruleForm) {
      console.log(this.$refs);
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.$emit("trans_infostep", this.info_step);
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
  },
  mounted() {
    getPmsCateWithChildren().then((data) => {
      // console.log("分类", data);
      this.options = data.data;
    });

    //获取所有的品牌数据
    getBrandList().then((data) => {
      // console.log("branddata", data);
      this.brandList = data.data;
    });
  },
};
</script>
<style scoped='scoped'>
.show_box {
  outline: 1px solid #ebeef5;
  width: 800px;
  height: 871.67px;
  margin: 20px 310px;
  padding: 40px 40px 20px;
}
.danwei {
  outline: 1px solid red;
  width: 20px;
}
.click_box {
  position: absolute;
  top: 840px;
  left: 860px;
}
.form_box {
  padding-top: 20px;
  width: 100%;
  height: 630px;
  float: left;
}
</style>
